import React, { Component } from 'react';
import { Typography } from '@material-ui/core';

class GolfLevelCompleted extends Component {
  constructor(props) {
    super(props);
    this.nextPhase = this.nextPhase.bind(this);
  }


  nextPhase() {
    this.props.gameFunc.update({ phase: 'gameplay' });
  }
  // p componentDidMount s starta en timer eller liknande. koppla timern till ngot visuellt. typ en materialUI progressbar. 0-100 som visas.
  // nr timern r klar s updateras phase till nsta

  render() {
    // vill jag ha en egen komponent som tergr till efter varje runda. eller ska jag gra som manygolf och lgga det i HUD?
    // s jag aldrig lmnar spelet fren alla banor r klara.
    // mindre att spara till firebase i guess. kan hlla round helt i state. fr generera om level. efter varje bana r klar.
    // kan ha manan i bakgrunden precis som i manygolf
    // anvnd drawHud till att rita leaderboard om level r done.
    return (
      <div className="phase-container">
        <Typography variant="h2">Golf leaderboard</Typography>
      </div>
    );
  }
}

export default GolfLevelCompleted;
